.button{
    background-color: chartreuse;
    height: 40px;
    padding: 0 10px;
    border-radius: 5px;
    width: 100%;
    font-size: 16px;
    color: #444;
    font-weight: bold;
    cursor: pointer;
    border: none;
    transition: .5s;
}
.button:hover{
    background-color: #444;
    color: chartreuse;
}
.button-container{
    margin-left: 10px;
    flex: 1;
}